<template>
  <div>
    <Header :activeI="activeIndex"></Header>
    <!--  -->
    <div class="container" v-loading="loadingStatic">
      <!-- el-tableheight -->
      <el-empty v-if="isEmpty" description=""
        ><router-link to="/"
          ><el-button></el-button></router-link
        ></el-empty
      >
      <GoodsCommon v-else v-for="item in goodsList" :key="item._id">
        <!--  -->
        <template v-slot:goodsImg>
          <!-- lazy   -->
          <el-image
            style="width: 60px; height: 60px"
            :src="item.goodsSRC"
            fit="fit"
            lazy
          ></el-image>
        </template>
        <!--  -->
        <template v-slot:goodsDetail>
          <h4>
            {{ item.goodsName }}
            <i style="margin-left:30px">{{ item.goodsPrice }}</i>
            <i style="margin-left:30px">{{ item.goodsSum }}</i>
          </h4>
          <p>{{ item.goodsDetail }}</p>
        </template>
        <!--  -->
        <template v-slot:goodsBuySum>
          <ShoppingCarSum
            :buySum2="item.buySum"
            :goodsSum2="item.goodsSum"
            :goodsId2="item._id"
          ></ShoppingCarSum>
        </template>
        <!--  -->
        <template v-slot:goodsBtn
          ><el-button type="danger" @click="onDelete(item.shoppingCarId)"
            ><b></b></el-button
          ></template
        >
        <!--  -->
        <template v-slot:goodsBuy
          ><el-button
            type="success"
            @click="
              onBuy(
                item._id,
                item.shoppingCarId,
                item.goodsName,
                item.goodsPrice
              )
            "
            ><b></b></el-button
          ></template
        >
      </GoodsCommon>
    </div>
  </div>
</template>
<script>
import request from '@/utils/request.js'
import GoodsCommon from '@/components/GoodsCommon/GoodsCommon'
import ShoppingCarSum from '@/components/GoodsCommon/ShoppingCarSum'

export default {
  name: 'ShoppingCar',
  data() {
    return {
      loadingStatic: true,
      // 
      isEmpty: true,
      // 
      activeIndex: '2',
      // 
      goodsList: []
    }
  },
  components: {
    GoodsCommon,
    ShoppingCarSum
  },
  created() {
    this.getGoods()
  },
  methods: {
    // 
    async getGoods() {
      const token = JSON.parse(localStorage.getItem('token'))
      // 
      const { data: res } = await request.get('/getShoppingCar', {
        params: {
          userId: token.userId
        }
      })
      // idres.goodsDataid
      if (res.goodsData !== '') {
        for (let i = 0; i < res.goodsData.length; i++) {
          const { data: res2 } = await request.get('/getGoodsOne', {
            params: {
              _id: res.goodsData[i].goodsID
            }
          })
          // ordergoods
          if (res2.goods === null) {
            request.get('/deleteShoopingCar', {
              params: {
                shoppingCarId: res.goodsData[i]._id
              }
            })
            break
          }
          res2.goods.buySum = res.goodsData[i].buySum
          res2.goods.shoppingCarId = res.goodsData[i]._id
          this.goodsList.push(res2.goods)
        }
      }
      this.isEmpty = res.isEmpty
      if (this.goodsList.length <= 0) {
        this.isEmpty = true
      }
      this.loadingStatic = false
    },
    // 
    async onBuy(goodsId, shoppingCarId, goodsName, goodsPrice) {
      // 
      const { data: DLSatate } = await request.get('/getAdminDL')
      if (DLSatate.data === 'false') {
        localStorage.removeItem('token')
      }
      const token = JSON.parse(localStorage.getItem('token'))
      if (!token) {
        this.$router.replace('/jumpLogin')
        return
      }
      // data={shoppingCarData: Array(...), goodsData: Array(...), isEmpty: false}
      const { data: res } = await request.get('/getShoppingCarOne', {
        params: {
          userId: token.userId,
          goodsId: goodsId
        }
      })
      this.$confirm(
        '<h3>' +
          goodsName +
          '</h3>' +
          '<h3>' +
          res.buySum +
          '</h3>' +
          '<h3>' +
          res.buySum * goodsPrice +
          '</h3>',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          dangerouslyUseHTMLString: true
        }
      )
        .then(() => {
          // 
          this.$confirm(
            '<h4">:</h4>' +
              '<h3 style="color:red">' +
              res.buySum * goodsPrice +
              '</h3>',
            '',
            {
              confirmButtonText: '',
              cancelButtonText: '',
              dangerouslyUseHTMLString: true
            }
          )
            .then(async () => {
              // 
              const result = await this.f3(goodsId, res.buySum)
              if (!result.state) {
                this.$alert('', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.go(0)
                  }
                })
              } else {
                // order
                const res2 = await this.f(goodsId, res.buySum, true)
                // 
                this.f2(shoppingCarId)
                //  goodsSum
                this.f4(goodsId, result.goodsSum - res.buySum)
                this.$router.go(0)
                if (res2.state) {
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration: '1000',
                    message: ''
                  })
                }
              }
            })
            .catch(async () => {
              // 
              const result = await this.f3(goodsId, res.buySum)
              if (!result.state) {
                this.$alert('', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.go(0)
                  }
                })
              } else {
                const res2 = await this.f(goodsId, res.buySum, false)
                // 
                this.f2(shoppingCarId)
                //  goodsSum
                this.f4(goodsId, result.goodsSum - res.buySum)
                this.$router.go(0)
                if (res2.state) {
                  this.$message({
                    type: 'error',
                    showClose: true,
                    duration: '1000',
                    message: ''
                  })
                }
              }
            })
        })
        .catch(() => {})
    },
    // order,this.onBuy()
    async f(id, buySum, receiveStateVal) {
      const goodsId = id
      const tokenData = JSON.parse(localStorage.getItem('token'))
      const { data: res2 } = await request.get('/addOrder', {
        params: {
          userId: tokenData.userId,
          goodsId,
          buySum,
          payState: receiveStateVal,
          deliverState: false,
          receiveState: false
        }
      })
      return res2
    },
    // ,this.onBuy()
    f2(shoppingCarId) {
      request.get('/deleteShoopingCar', {
        params: {
          shoppingCarId
        }
      })
    },
    // 
    async f3(goodsId, buySum) {
      const { data: res } = await request.get('/getGoodsOne', {
        params: {
          _id: goodsId
        }
      })
      if (buySum <= res.goods.goodsSum) {
        return { state: true, goodsSum: res.goods.goodsSum }
      } else {
        return false
      }
    },
    //  goodsSum
    f4(goodsId, goodsSum) {
      request.get('/updateGoodsSum', {
        params: {
          goodsId,
          goodsSum
        }
      })
    },
    // 
    async onDelete(shoppingCarId) {
      // 
      const { data: DLSatate } = await request.get('/getAdminDL')
      if (DLSatate.data === 'false') {
        localStorage.removeItem('token')
      }
      const token = JSON.parse(localStorage.getItem('token'))
      if (!token) {
        this.$router.replace('/jumpLogin')
        return
      }
      this.$confirm(
        '<h4"></h4>' + '<h3 style="color:red">' + '' + '</h3>',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          dangerouslyUseHTMLString: true
        }
      )
        .then(() => {
          request.get('deleteShoopingCar', {
            params: {
              shoppingCarId
            }
          })
          // 
          this.$router.go(0)
        })
        .catch(() => {})
    }
  }
}
</script>
<style lang="less" scoped></style>
