<template>
  <div class="AdminGoods" v-loading="loading">
    <!--  -->
    <HeaderAdmin>
      <template v-slot:search>
        <div style="width:50%">
          <el-input
            style="width:40%;"
            v-model="searchInput"
            placeholder=""
          ></el-input>
          <el-button
            type="primary"
            style="width:20%;max-width:80px;margin-left:10%"
            @click="add"
            ></el-button
          >
        </div>
      </template>
    </HeaderAdmin>
    <div class="AdminContent">
      <el-empty v-if="isEmpty"></el-empty>
      <GoodsCommon v-else v-for="(item, index) in goodsList" :key="item._id">
        <!--  -->
        <template v-slot:goodsImg>
          <!-- lazy   -->
          {{ index + 1 > 9 ? index + 1 : '0' + (index + 1) }}
          <el-image
            style="width: 60px; height: 60px;"
            :src="item.goodsSRC"
            fit="fit"
            lazy
          ></el-image>
        </template>
        <!--  -->
        <template v-slot:goodsDetail>
          <h4>
            {{ item.goodsName }}
            <i style="margin-left:20px">{{ item.goodsPrice }}</i>
            <i style="margin-left:20px">{{ item.goodsOldPrice }}</i>
            <i style="margin-left:20px">{{ item.goodsSum }}</i>
          </h4>
          <p>{{ item.goodsDetail }}</p>
        </template>
        <!--  -->
        <template v-slot:goodsBuySum> </template>
        <!--  -->
        <template v-slot:goodsBtn
          ><el-button type="danger" @click="dl(item._id)"
            ><b></b></el-button
          ></template
        >
        <!--  -->
        <template v-slot:goodsBuy>
          <el-button type="success" @click="up(item._id)"
            ><b></b></el-button
          >
        </template>
      </GoodsCommon>
    </div>
    <div class="block">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        :total="count"
        @current-change="getGoods"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
// axios
import request from '@/utils/request.js'
import GoodsCommon from '@/components/GoodsCommon/GoodsCommon'

export default {
  data() {
    return {
      name: 'AdminGoods',
      // 
      searchInput: '',
      // 
      count: 0,
      // 12
      pageSize: 12,
      // 
      Skip: 0,
      // 
      pageNum2: 0,
      // 
      currentPage: 1,
      isEmpty: true,
      loading: true,
      goodsList: [],
      // 
      goodsList2: []
    }
  },
  components: {
    GoodsCommon
  },
  created() {
    this.getGoods()
  },
  watch: {
    // 
    searchInput() {
      // 1
      this.currentPage = 1
      // 
      this.pageNum2 = 1
      this.searchBtn()
    },
    // 
    pageNum2() {
      if (this.searchInput !== '') {
        this.searchBtn()
      }
    }
  },
  methods: {
    // gooodsList
    async getGoods(pageNum, search) {
      if (pageNum === undefined) {
        pageNum = 1
      }
      this.pageNum2 = pageNum
      // 
      if (this.searchInput !== '') {
        if (search === true) {
          this.searchBtn()
        }
        return
      }
      this.loading = true
      if (pageNum < this.count / 12 + 1) {
        this.Skip = 12 * (pageNum - 1)
      }
      const { data: res } = await request.get('/getGoods', {
        params: {
          count: this.Skip
        }
      })
      this.goodsList = []
      this.goodsList = res.data
      this.count = res.goodsCount
      if (this.goodsList.length > 0) {
        this.isEmpty = false
      } else {
        this.isEmpty = true
      }
      this.loading = false
    },
    // 
    async searchBtn() {
      this.loading = true
      const { data: res } = await request.get('/getGoodsName', {
        params: {
          goodsName: this.searchInput
        }
      })
      if (res.isEmpty) {
        this.isEmpty = res.isEmpty
        this.loading = false
        this.count = 0
      } else {
        this.goodsList = []
        this.goodsList = res
        this.goodsList2 = res
        this.count = res.length
        // 
        if (this.goodsList2.length > 12) {
          const goodsLista = this.goodsList2
          for (let i = 0; i < (this.pageNum2 - 1) * 12; i++) {
            // 
            goodsLista.shift()
          }
          // 0-1112
          this.goodsList = goodsLista.slice(0, 12)
        }
        this.isEmpty = false
        this.loading = false
      }
    },
    // 
    add() {
      this.$router.replace('/jumpAdminAddGoods')
    },
    // 
    dl(goodsId) {
      request.get('/deleteGoods', {
        params: {
          goodsId
        }
      })
      alert('')
      this.getGoods(this.pageNum2, true)
    },
    // 
    up(goodsId) {
      this.$router.replace('/jumpAdminUpdateGoods?goodsId=' + goodsId)
    }
  }
}
</script>
<style lang="less" scoped>
.AdminContent {
  margin-top: 2px;
  width: 1100px;
  overflow: auto;
}

.block {
  margin: 10px 0 20px 0;
}
</style>
