<template>
  <div>
    <Header :activeI="activeIndex"></Header>
    <!--  -->
    <div class="container">
      <h2 style="padding:30px 0 30px 0">{{ username }}</h2>
      <el-button @click="updateUser"></el-button>
    </div>
  </div>
</template>
<script>
import request from '@/utils/request.js'

export default {
  name: 'ShoppingCar',
  data() {
    return {
      // 
      loading: false,
      activeIndex: '4-1',
      username: ''
    }
  },
  created() {
    this.getUser()
  },
  methods: {
    getUser() {
      this.username = JSON.parse(localStorage.getItem('token')).username
    },
    // 
    updateUser() {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: ''
      })
        .then(async ({ value }) => {
          const { data: res } = await request.get('/getUser', {
            params: {
              username: this.username,
              password: value
            }
          })
          if (res.data === this.username) {
            this.$prompt('', '', {
              confirmButtonText: '',
              cancelButtonText: ''
            })
              .then(async ({ value }) => {
                const { data: res } = await request.get('/updateUser', {
                  params: {
                    username: this.username,
                    password: value
                  }
                })
                this.$message({
                  type: 'success',
                  message: res.data,
                  duration: '1000'
                })
              })
              .catch(() => {})
          } else {
            this.$message({
              type: 'error',
              message: res.data,
              duration: '2000'
            })
          }
        })
        .catch(() => {})
    }
  }
}
</script>
<style lang="less" scoped></style>
