<template>
  <div class="AdminAddGoodsContent">
    <el-form
      label-position="right"
      :rules="rules"
      ref="goodsFrom"
      label-width="80px"
      :model="goodsFrom"
    >
      <el-form-item label="" prop="goodsName">
        <el-input v-model="goodsFrom.goodsName"></el-input>
      </el-form-item>

      <el-form-item label="" prop="goodsSum">
        <el-input-number
          v-model="goodsFrom.goodsSum"
          :min="0"
        ></el-input-number>
      </el-form-item>

      <el-form-item label="" prop="goodsSRC">
        <el-input v-model="goodsFrom.goodsSRC"></el-input>
      </el-form-item>

      <el-form-item label="" prop="goodsPrice">
        <el-input v-model="goodsFrom.goodsPrice"></el-input>
      </el-form-item>

      <el-form-item label="" prop="goodsOldPrice">
        <el-input v-model="goodsFrom.goodsOldPrice"></el-input>
      </el-form-item>

      <el-form-item label="" prop="goodsDetail">
        <el-input
          type="textarea"
          :rows="3"
          placeholder=""
          v-model="goodsFrom.goodsDetail"
          maxlength="50"
          show-word-limit
        >
        </el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="submitForm('goodsFrom')"
          ></el-button
        >
        <el-button @click="resetForm('goodsFrom')"></el-button>
        <el-button @click="jumpGo()"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// axios
import request from '@/utils/request.js'

export default {
  data() {
    return {
      name: 'AdminAddGoods',
      //   from
      goodsFrom: {
        goodsName: '',
        goodsSum: 0,
        goodsSRC: 'https://qc9c8t.app.cloudendpoint.cn/',
        goodsPrice: '',
        goodsOldPrice: '',
        goodsDetail: ''
      },
      //   from
      rules: {
        goodsName: [{ required: true, message: '', trigger: 'blur' }],
        goodsSum: [{ required: true, message: '', trigger: 'blur' }],
        goodsSRC: [{ required: true, message: '', trigger: 'blur' }],
        goodsPrice: [{ required: true, message: '', trigger: 'blur' }],
        goodsDetail: [
          { max: 50, message: '50', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    submitForm(goodsFrom) {
      this.$refs[goodsFrom].validate(valid => {
        if (valid) {
          request.post('/addGoodsOne', this.goodsFrom)
          alert('')
          this.resetForm('goodsFrom')
        } else {
          alert('')
          return false
        }
      })
    },
    // 
    resetForm(goodsFrom) {
      this.$refs[goodsFrom].resetFields()
    },
    // 
    jumpGo() {
      this.$router.replace('/jumpAdmin/jumpAdminGoods')
    }
  }
}
</script>
<style lang="less" scoped>
.AdminAddGoodsContent {
  width: 500px;
  margin: 20px auto;
}
</style>
