<template>
  <div>
    <el-button @click="reduce">-</el-button>
    <el-input
      style="width:100px;margin-left:10px;margin-right:10px"
      type="number"
      v-model="buySum"
    />
    <el-button @click="add">+</el-button>
  </div>
</template>
<script>
import request from '@/utils/request.js'

export default {
  props: ['buySum2', 'goodsSum2', 'goodsId2'],
  data() {
    return {
      name: 'ShoppingCarSum',
      buySum: this.buySum2,
      goodsSum: this.goodsSum2
    }
  },
  watch: {
    // <1>
    async buySum(newVal, oldVal) {
      // 
      const { data: DLSatate } = await request.get('/getAdminDL')
      if (DLSatate.data === 'false') {
        localStorage.removeItem('token')
      }
      const token = JSON.parse(localStorage.getItem('token'))
      if (!token) {
        this.$router.replace('/jumpLogin')
        return
      }
      if (this.goodsSum > 0) {
        // eE
        const buySum = this.buySum.toString()
        var arr = newVal.toString()
        // 1-9
        arr = arr.charAt(0)
        if (newVal !== '') {
          if (newVal > this.goodsSum) {
            this.buySum = this.goodsSum
          } else if (buySum.indexOf('e') === 1 || buySum.indexOf('E') === 1) {
            this.$message({
              type: 'error',
              showClose: true,
              duration: '1000',
              message: ''
            })
            this.buySum = 1
          } else if (arr.charCodeAt() <= 48 || arr.charCodeAt() > 57) {
            this.$message({
              type: 'error',
              showClose: true,
              duration: '1000',
              message: ''
            })
            this.buySum = 1
          } else {
            this.setShoppingCarSum()
          }
        } else {
          this.$message({
            type: 'error',
            showClose: true,
            duration: '1000',
            message: ''
          })
        }
      } else {
        this.$alert('', '', {
          confirmButtonText: ''
        })
      }
    }
  },
  created() {},
  methods: {
    // +1
    add() {
      const buySum = this.buySum.toString()
      // +-buySum
      if (buySum === '') {
        this.buySum = 1
      } else if (this.buySum < this.goodsSum) {
        this.buySum = parseInt(this.buySum)
        this.buySum += 1
      }
    },
    // -1
    reduce() {
      const buySum = this.buySum.toString()
      // +-buySum
      if (buySum === '') {
        this.buySum = 1
      } else if (this.buySum > 1) {
        this.buySum = parseInt(this.buySum)
        this.buySum -= 1
      }
    },
    // ,buySum(),
    async setShoppingCarSum() {
      const token = JSON.parse(localStorage.getItem('token'))
      const { data: res } = await request.get('/updataShoopingCar', {
        params: {
          userId: token.userId,
          goodsId: this.goodsId2,
          buySum: this.buySum
        }
      })
      this.$message({
        type: 'success',
        showClose: true,
        duration: '1000',
        message: res.news
      })
    }
  }
}
</script>
<style lang="less" scoped></style>
