<template>
  <div>
    <!--  -->
    <Header :activeI="activeIndex"></Header>
    <div class="container" v-loading="loadingStatic">
      <!--  -->
      <el-card class="box-card">
        <el-carousel indicator-position="outside">
          <el-carousel-item v-for="item in imgs" :key="item">
            <img :src="item" alt="" />
          </el-carousel-item>
        </el-carousel>
      </el-card>
      <!--  -->
      <el-card class="box-card">
        <div slot="header" class="clearfix">
          <h3></h3>
        </div>
        <div style="overflow:auto;height: 640px;margin-bottom: 20px;">
          <ul
            v-infinite-scroll="load"
            infinite-scroll-disabled="disabled"
            infinite-scroll-immediate="false"
          >
            <li v-for="item in goodsList" :key="item._id">
              <router-link :to="'/jumpDetail?_id=' + item._id">
                <div class="image">
                  <img :src="item.goodsSRC" alt="" />
                </div>
                <div class="title">
                  <h2>{{ item.goodsName }}</h2>
                </div>
                <div class="price">
                  <p>
                    <del>{{ item.goodsOldPrice }}</del>
                  </p>
                  <p>{{ item.goodsPrice }}</p>
                  <p>{{ item.goodsSum }}</p>
                </div>
              </router-link>
            </li>
          </ul>
          <p v-if="loading" style="color:red">...</p>
          <p v-if="noMore" style="color:red"></p>
        </div>
        <p v-if="!noMore"></p>
      </el-card>
    </div>
  </div>
</template>
<script>
// axios
import request from '@/utils/request.js'

export default {
  name: 'Goods',
  data() {
    return {
      loadingStatic: true,
      // 
      loading: false,
      // 
      activeIndex: '1',
      //  Skip 
      Skip: 0,
      // 
      count: 0,
      goodsList: [],
      // 
      imgs: [
        'https://img11.360buyimg.com/n1/jfs/t1/179770/17/12742/145428/60e2ec6bE197a8dd5/fc13551194e20ba0.jpg',
        'https://img11.360buyimg.com/n1/jfs/t1/208131/34/1381/105367/614a9633Ea9c487bd/6cac5f26c55eacd6.jpg',
        'https://img12.360buyimg.com/n1/jfs/t1/127643/39/19646/110844/60b4975cE7b229c7b/71f18956d90684ea.jpg'
      ]
    }
  },
  created() {
    this.getGoods()
  },
  disabled() {
    this.loadingStatic = true
  },
  // 
  computed: {
    // 
    noMore() {
      return this.goodsList.length >= this.count
    },
    disabled() {
      return this.loading || this.noMore
    }
  },
  methods: {
    // 
    load() {
      this.loading = true
      setTimeout(() => {
        if (this.Skip < this.count) {
          this.Skip += 12
        }
        this.getGoods()
        this.loading = false
      }, 1000)
    },
    // gooodsList
    async getGoods() {
      const { data: res } = await request.get('/getGoods', {
        params: {
          count: this.Skip
        }
      })
      this.goodsList = [...this.goodsList, ...res.data]
      this.count = res.goodsCount
      this.loadingStatic = false
    }
  }
}
</script>
<style lang="less" scoped>
// ul
.box-card ul::after {
  content: '';
  display: block;
  clear: both;
  visibility: hidden;
}

.box-card ul {
  border-width: 2px 0px 0px 2px;
  border-color: #e0e0e0;
  border-style: solid;
}

.box-card ul li {
  box-sizing: border-box;
  float: left;
  height: 320px;
  width: 25%;
  border-width: 0px 2px 2px 0px;
  border-color: #e0e0e0;
  border-style: solid;
}

.box-card ul li:hover {
  cursor: pointer;
}

.box-card ul li:hover img {
  width: 70%;
}

.box-card ul li:hover img {
  margin: 0 15%;
}

.image {
  width: 100%;
  height: 210px;
  line-height: 210px;
}

img {
  width: 55%;
  margin: 10% 45/2%;
}

.title,
.price {
  width: 100%;
  text-align: center;
}

.title h2 {
  padding: 0 20px;

  white-space: nowrap; /*  */
  overflow: hidden; /*  */
  text-overflow: ellipsis;
}

.price {
  font-size: 15px;
}

.price p:nth-child(1) {
  color: red;
}
</style>
