<template>
  <div class="DetailContainer">
    <ul>
      <li>
        <div class="image">
          <img :src="goods.goodsSRC" alt="" />
        </div>
      </li>
      <li>
        <el-form label-width="100px">
          <el-form-item label="">
            <h2>{{ goods.goodsName }}</h2>
          </el-form-item>
          <el-form-item label="">
            <h2 style="color:red">{{ goods.goodsPrice }}</h2>
          </el-form-item>
          <el-form-item label="">
            <p style="word-wrap:break-word; ">{{ goods.goodsDetail }}</p>
          </el-form-item>
          <el-form-item label="">
            <p>{{ goods.goodsSum }}</p>
          </el-form-item>
          <el-form-item style="margin-left:-10px" label=":">
            <el-row>
              <el-button @click="reduce">-</el-button>
              <el-input
                style="width:100px;margin-left:10px;margin-right:10px"
                type="number"
                v-model="buySum"
              />
              <el-button @click="add">+</el-button>
            </el-row>
          </el-form-item>
          <el-form-item label-width="30px">
            <el-button type="primary" @click="onBuy"></el-button>
            <el-button type="danger" @click="addShoppingCar"
              ></el-button
            >
            <el-button style="margin-left:80px" @click="goBack"></el-button>
          </el-form-item>
        </el-form>
      </li>
    </ul>
  </div>
</template>
<script>
import request from '../../utils/request'

export default {
  name: 'Detail',
  data() {
    return {
      buySum: 1,
      goods: {}
    }
  },
  created() {
    this.getGoodsDetail()
  },
  watch: {
    // <1>
    buySum(newVal, oldVal) {
      if (this.goods.goodsSum > 0) {
        // eE
        const buySum = this.buySum.toString()
        var arr = newVal.toString()
        // 1-9
        arr = arr.charAt(0)
        if (newVal !== '') {
          if (Number(newVal) > this.goods.goodsSum) {
            this.buySum = this.goods.goodsSum
          } else if (buySum.indexOf('e') === 1 || buySum.indexOf('E') === 1) {
            this.$message({
              type: 'error',
              showClose: true,
              duration: '1000',
              message: ''
            })
            this.buySum = 1
          } else if (arr.charCodeAt() <= 48 || arr.charCodeAt() > 57) {
            this.$message({
              type: 'error',
              showClose: true,
              duration: '1000',
              message: ''
            })
            this.buySum = 1
          }
        } else {
          this.$message({
            type: 'error',
            showClose: true,
            duration: '1000',
            message: ''
          })
        }
      } else {
        this.$alert('', '', {
          confirmButtonText: ''
        })
      }
    }
  },
  methods: {
    // 
    async getGoodsDetail() {
      const _id = location.hash.split('?')[1].split('=')[1]
      const { data: res } = await request.get('/getGoodsOne', {
        params: {
          _id: _id
        }
      })
      this.goods = res.goods
    },

    // +1
    add() {
      const buySum = this.buySum.toString()
      // +-buySum
      if (buySum === '') {
        this.buySum = 1
      } else if (this.buySum < this.goods.goodsSum) {
        this.buySum = parseInt(this.buySum)
        this.buySum += 1
      }
    },
    // -1
    reduce() {
      const buySum = this.buySum.toString()
      // +-buySum
      if (buySum === '') {
        this.buySum = 1
      } else if (this.buySum > 1) {
        this.buySum = parseInt(this.buySum)
        this.buySum -= 1
      }
    },
    // 
    async onBuy() {
      // 
      const { data: DLSatate } = await request.get('/getAdminDL')
      if (DLSatate.data === 'false') {
        localStorage.removeItem('token')
      }
      const token = JSON.parse(localStorage.getItem('token'))
      if (!token) {
        this.$router.replace('/jumpLogin')
        return
      }
      const buySum = this.buySum.toString()
      if (buySum === '') {
        this.$message({
          type: 'error',
          showClose: true,
          duration: '1000',
          message: ''
        })
        this.buySum = 1
        return
      }
      this.$confirm(
        '<h3>' +
          this.goods.goodsName +
          '</h3>' +
          '<h3>' +
          this.buySum +
          '</h3>' +
          '<h3>' +
          this.buySum * this.goods.goodsPrice +
          '</h3>',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          dangerouslyUseHTMLString: true
        }
      )
        .then(() => {
          // 
          this.$confirm(
            '<h4">:</h4>' +
              '<h3 style="color:red">' +
              this.buySum * this.goods.goodsPrice +
              '</h3>',
            '',
            {
              confirmButtonText: '',
              cancelButtonText: '',
              dangerouslyUseHTMLString: true
            }
          )
            .then(async () => {
              // 
              const result = await this.f2()
              if (!result.state) {
                this.$alert('', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.go(0)
                  }
                })
              } else {
                // 
                const res = await this.f(true)
                //  goodsSum
                this.f3(result.goodsSum - this.buySum)
                this.$router.go(0)
                if (res.state) {
                  this.$message({
                    type: 'success',
                    showClose: true,
                    duration: '1000',
                    message: ''
                  })
                }
              }
            })
            .catch(async () => {
              // 
              const result = await this.f2()
              if (!result.state) {
                this.$alert('', '', {
                  confirmButtonText: '',
                  callback: () => {
                    this.$router.go(0)
                  }
                })
              } else {
                // 
                const res = await this.f(false)
                //  goodsSum
                this.f3(result.goodsSum - this.buySum)
                this.$router.go(0)
                if (res.state) {
                  this.$message({
                    type: 'error',
                    showClose: true,
                    duration: '1000',
                    message: ''
                  })
                }
              }
            })
        })
        .catch(() => {})
    },
    // order,this.onBuy()
    async f(receiveStateVal) {
      const goodsId = location.hash.split('?')[1].split('=')[1]
      const tokenData = JSON.parse(localStorage.getItem('token'))
      const { data: res } = await request.get('/addOrder', {
        params: {
          userId: tokenData.userId,
          goodsId,
          buySum: this.buySum,
          payState: receiveStateVal,
          deliverState: false,
          receiveState: false
        }
      })
      return res
    },
    // 
    async f2() {
      const goodsId = location.hash.split('?')[1].split('=')[1]
      const { data: res } = await request.get('/getGoodsOne', {
        params: {
          _id: goodsId
        }
      })
      if (this.buySum <= res.goods.goodsSum) {
        return { state: true, goodsSum: res.goods.goodsSum }
      } else {
        return false
      }
    },
    //  goodsSum
    f3(goodsSum) {
      const goodsId = location.hash.split('?')[1].split('=')[1]
      request.get('/updateGoodsSum', {
        params: {
          goodsId,
          goodsSum
        }
      })
    },
    // 
    async addShoppingCar() {
      // 
      const { data: DLSatate } = await request.get('/getAdminDL')
      if (DLSatate.data === 'false') {
        localStorage.removeItem('token')
      }
      const token = JSON.parse(localStorage.getItem('token'))
      if (!token) {
        this.$router.replace('/jumpLogin')
        return
      }
      if (this.goods.goodsSum <= 0) {
        this.$alert('', '', {
          confirmButtonText: ''
        })
        return
      }
      const buySum = this.buySum.toString()
      if (buySum === '') {
        this.$message({
          type: 'error',
          showClose: true,
          duration: '1000',
          message: ''
        })
        this.buySum = 1
        return
      }
      // goods.vuelocation.hash
      const goodsId = location.hash.split('?')[1].split('=')[1]
      const tokenData = JSON.parse(localStorage.getItem('token'))
      const { data: res } = await request.get('/addShoppingCar', {
        params: {
          userId: tokenData.userId,
          goodsId,
          buySum: this.buySum,
          goodsSum: this.goods.goodsSum
        }
      })
      if (res.static) {
        this.$message({
          type: 'success',
          showClose: true,
          duration: '1000',
          message: res.news
        })
      }
    },
    // 
    goBack() {
      this.$router.replace('/')
    }
  }
}
</script>
<style lang="less" scoped>
.DetailContainer {
  width: 80%;
  margin: 20px auto;
}

ul li {
  float: left;
  width: 50%;
}

.el-form {
  margin-top: 30px;
}

img {
  width: 80%;
  margin-left: 10%;
}
</style>
