<template>
  <div class="AdminGoods" v-loading="loading">
    <!--  -->
    <HeaderAdmin>
      <template v-slot:search>
        <div style="width:50%;min-width:180px">
          <el-input
            style="width:40%;"
            v-model="searchInput"
            placeholder=""
          ></el-input>
          <el-button
            type="primary"
            style="width:20%;max-width:100px;margin-left:5%"
            @click="DL"
            >{{ isDL ? '' : '' }}</el-button
          >
          <el-button
            type="primary"
            style="width:20%;max-width:100px;margin-left:5%"
            @click="ZC"
            >{{ isZC ? '' : '' }}</el-button
          >
        </div>
      </template>
    </HeaderAdmin>
    <div class="AdminContent">
      <el-empty v-if="isEmpty"></el-empty>
      <GoodsCommon v-else v-for="(item, index) in usersList" :key="item._id">
        <!--  -->
        <template v-slot:goodsImg>
          <!-- lazy   -->
          {{ index + 1 > 9 ? index + 1 : '0' + (index + 1) }}
        </template>
        <!--  -->
        <template v-slot:goodsDetail>
          <h4>
            <i style="margin-left:20px">{{ item.username }}</i>
          </h4>
        </template>
        <!--  -->
        <template v-slot:goodsBuySum> </template>
        <!--  -->
        <template v-slot:goodsBtn
          ><el-button type="danger" @click="dl(item._id)"
            ><b></b></el-button
          ></template
        >
        <!--  -->
        <template v-slot:goodsBuy>
          <el-button type="success" @click="prohibit(item._id, item.prohibit)"
            ><b>{{
              item.prohibit === 'false' ? '' : ''
            }}</b></el-button
          >
        </template>
      </GoodsCommon>
    </div>
    <div class="block">
      <el-pagination
        background
        layout="prev, pager, next"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        :total="count"
        @current-change="getUsers"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
// axios
import request from '@/utils/request.js'
import GoodsCommon from '@/components/GoodsCommon/GoodsCommon'

export default {
  data() {
    return {
      name: 'AdminGoods',
      // 
      searchInput: '',
      // 
      count: 0,
      // 12
      pageSize: 12,
      // 
      currentPage: 1,
      // 
      Skip: 0,
      // 
      pageNum2: 0,
      // 
      isDL: true,
      // 
      isZC: true,
      isEmpty: true,
      loading: true,
      usersList: [],
      // 
      usersList2: []
    }
  },
  components: {
    GoodsCommon
  },
  created() {
    this.getUsers()
  },
  watch: {
    // 
    searchInput() {
      // 1
      this.currentPage = 1
      // 
      this.pageNum2 = 1
      this.searchBtn()
    },
    // 
    pageNum2() {
      if (this.searchInput !== '') {
        this.searchBtn()
      }
    }
  },
  methods: {
    // usersList
    async getUsers(pageNum, search) {
      if (pageNum === undefined) {
        pageNum = 1
      }
      this.pageNum2 = pageNum
      // 
      if (this.searchInput !== '') {
        if (search === true) {
          this.searchBtn()
        }
        return
      }
      this.loading = true
      if (pageNum < this.count / 12 + 1) {
        this.Skip = 12 * (pageNum - 1)
      }
      const { data: res } = await request.get('/getUsers', {
        params: {
          count: this.Skip
        }
      })
      this.usersList = []
      this.usersList = res.data
      this.count = res.goodsCount
      // 
      const { data: res2 } = await request.get('/getAdminDL')
      switch (res2.data) {
        case 'true':
          this.isDL = true
          break
        case 'false':
          this.isDL = false
          break
      }
      // 
      const { data: res3 } = await request.get('/getAdminZC')
      switch (res3.data) {
        case 'true':
          this.isZC = true
          break
        case 'false':
          this.isZC = false
          break
      }
      // userList
      if (this.usersList.length > 0) {
        this.isEmpty = false
      } else {
        this.isEmpty = true
      }
      this.loading = false
    },
    async DL() {
      this.isDL = !this.isDL
      const { data: res } = await request.get('/setAdminDL', {
        params: {
          DL: this.isDL
        }
      })
      alert(res.data)
    },
    async ZC() {
      this.isZC = !this.isZC
      const { data: res } = await request.get('/setAdminZC', {
        params: {
          ZC: this.isZC
        }
      })
      alert(res.data)
    },
    // 
    async searchBtn() {
      this.loading = true
      const { data: res } = await request.get('/getUsersName', {
        params: {
          usersName: this.searchInput
        }
      })
      if (res.isEmpty) {
        this.isEmpty = res.isEmpty
        this.loading = false
        this.count = 0
      } else {
        this.usersList = []
        this.usersList = res
        this.usersList2 = res
        this.count = res.length
        // 
        if (this.usersList2.length > 12) {
          const usersLista = this.usersList2
          // 
          for (let i = 0; i < (this.pageNum2 - 1) * 12; i++) {
            usersLista.shift()
          }
          // 0-1112
          this.usersList = usersLista.slice(0, 12)
        }
        this.isEmpty = false
        this.loading = false
      }
    },
    // 
    dl(userId) {
      request.get('/deleteUser', {
        params: {
          userId
        }
      })
      alert('')
      this.getUsers(this.pageNum2, true)
    },
    //  
    async prohibit(userId, prohibit) {
      switch (prohibit) {
        case 'true':
          prohibit = 'false'
          break
        case 'false':
          prohibit = 'true'
          break
      }
      const { data: res } = await request.get('/updateUserLogin', {
        params: {
          userId,
          prohibit: prohibit
        }
      })
      alert(res.data)
      this.getUsers(this.pageNum2)
    }
  }
}
</script>
<style lang="less" scoped>
.AdminContent {
  margin-top: 2px;
  width: 1100px;
  overflow: auto;
}

.block {
  margin: 10px 0 20px 0;
}
</style>
