<template>
  <div class="positionDiv" v-loading="loadingStatic">
    <div class="tt"><h1></h1></div>
    <div>
      <label for="username"> </label>
      <el-input
        id="username"
        placeholder=""
        v-model="username"
        clearable
      ></el-input>
    </div>
    <div>
      <label for="password"> </label>
      <el-input
        id="password"
        placeholder=""
        v-model="password"
        show-password
      ></el-input>
    </div>
    <div>
      <label for="password2"></label>
      <el-input
        id="password2"
        placeholder=""
        v-model="password2"
        show-password
      ></el-input>
    </div>
    <div class="tj">
      <el-button @click="submin"></el-button>
      <el-button @click="reset"></el-button>
      <el-button @click="jumpLogin"></el-button>
      <el-button @click="jumpIndex"></el-button>
    </div>
  </div>
</template>

<script>
import request from '../../utils/request'

export default {
  name: 'Login',
  data() {
    return {
      loadingStatic: true,
      username: '',
      password: '',
      password2: ''
    }
  },
  mounted() {
    this.loadingStatic = false
  },
  destroyed() {
    this.loadingStatic = true
  },
  methods: {
    // 
    async submin() {
      this.loadingStatic = true
      // 
      const { data: ZCState } = await request.get('/getAdminZC')
      if (ZCState.data === 'false') {
        alert('')
        this.loadingStatic = false
        return
      }
      if (this.password === '' || this.password === '') {
        this.loadingStatic = false
        this.$message.error('')
      } else if (this.password === this.password2) {
        const { data: userData } = await request.post('/addUser', {
          username: this.username,
          password: this.password
        })
        this.loadingStatic = false
        // userData.datadt.username
        if (userData.data === this.username) {
          const tokenData = {
            userId: userData.userId,
            username: userData.data
          }
          localStorage.setItem('token', JSON.stringify(tokenData))
          this.$message({
            message: '' + userData.data,
            type: 'success'
          })
          this.$router.replace('/')
        } else {
          this.loadingStatic = false
          this.$message.error(userData.data)
        }
      } else {
        this.loadingStatic = false
        this.$message.error('')
      }
    },
    reset() {
      this.username = ''
      this.password = ''
      this.password2 = ''
    },
    jumpLogin() {
      this.$router.replace('/jumpLogin')
    },
    jumpIndex() {
      this.$router.replace('/')
    }
  }
}
</script>

<style lang="less" scoped>
.positionDiv {
  width: 450px;
  margin: 100px auto;
}

// 
.tt {
  width: 180px;
  margin: 0 auto;
}

.positionDiv div {
  margin-bottom: 15px;
}

.tj {
  display: flex;
  justify-content: space-around;
}
</style>
